<template>
  <div class="flex flex-col items-center justify-between h-full">
    <div class="flex flex-col items-center gap-y-8">
      <div class="flex relative w-min">
        <div class="w-72 aspect-square">
          <Progress :value="progress" />
        </div>

        <div
          class="absolute top-1/2 left-1/2 -translate-x-1/2 -translate-y-1/2 text-3xl"
        >
          {{ counter.value }}
        </div>
      </div>

      <div
        class="flex flex-col gap-y-2"
        :class="counter.isRunning.value && 'invisible'"
      >
        <button
          :class="counter.value.value === timer.time && 'invisible'"
          class="button"
          @click="counter.reset"
        >
          Reset
        </button>

        <div class="flex gap-x-2">
          <button class="button" @click="handleTimerChange(timerIndex - 1)">
            Prev
          </button>
          <button class="button" @click="handleTimerChange(timerIndex + 1)">
            Next
          </button>
        </div>
      </div>
    </div>

    <div class="flex flex-col" :class="isExerciseDone ? 'text-xl' : 'text-3xl'">
      <div v-if="isExerciseDone">Done:</div>
      <div>{{ timer.name }}</div>
    </div>

    <div
      v-if="timerIndex < profile.timers.length - 1"
      class="flex flex-col"
      :class="isExerciseDone ? 'text-3xl' : 'text-xl'"
    >
      <div>Next:</div>
      <div>{{ profile.timers[timerIndex + 1].name }}</div>
    </div>

    <div class="flex flex-col w-40">
      <button v-if="isFinished" class="button flex-grow" @click="handleFinish">
        Finish
      </button>
      <button
        v-else-if="counter.value.value <= 0"
        class="button flex-grow"
        @click="handleNext"
      >
        Start next
      </button>
      <button
        v-else
        class="button flex-grow"
        @click="counter.isRunning.value ? counter.pause() : counter.start()"
      >
        {{ counter.isRunning.value ? 'Pause' : 'Start' }}
      </button>
    </div>
  </div>
</template>

<script setup>
import { computed, onMounted, ref, watchEffect } from 'vue'
import { useRoute, useRouter } from 'vue-router'

import { useSettingsStore } from '@/stores/settings'
import { useCounter } from '@/use/counter'

import Progress from '@/components/progress/Progress.vue'

const router = useRouter()
const route = useRoute()

const { settings } = useSettingsStore()
const { profiles } = settings

const profile = profiles.find(({ id }) => id === route.params.id)

const timerIndex = ref(0)
const timer = computed(() => profile.timers[timerIndex.value])

const isFinished = ref(false)

const counter = useCounter(timer.value.time)

const progress = computed(() => 1 - counter.value.value / timer.value.time)
const isExerciseDone = computed(() => progress.value === 1)

function handleTimerChange(nextTimerIndex) {
  if (nextTimerIndex < 0 || nextTimerIndex > profile.timers.length - 1) {
    return
  }

  timerIndex.value = nextTimerIndex
  counter.change(timer.value.time)
}

function handleNext() {
  handleTimerChange(timerIndex.value + 1)
  counter.start()
}

function handleFinish() {
  router.replace({ name: 'profiles' })
}

watchEffect(() => {
  if (counter.value.value === 0) {
    if (timerIndex.value < profile.timers.length - 1) {
      // timerIndex.value += 1
    } else {
      isFinished.value = true
    }
  }
})

onMounted(async () => {
  try {
    await navigator.wakeLock.request()
  } catch (error) {
    console.error(error)
  }
})
</script>
